import React, { createRef, Fragment, PureComponent } from 'react';
import { createPortal } from 'react-dom'
import domtoimage from 'dom-to-image';

import Link from 'components/Link';
import Box from 'components/Box';
import Flex from 'components/Flex';
import Circle from 'components/Circle';
import Download from 'components/Download';
import Text from 'components/Text';
import Button from 'components/Button';
import StyledImage from 'components/Image';
import Title from 'components/Title';
import Container from 'components/Container';
import theme, { mobileOrDesktop } from 'components/ThemeProvider/theme';
import withResponsive from 'hoc/withResponsive'

import saveAs from 'vendor/FileSaver'

import DateCard from '../MemoPage/DateCard'
import MemoResult from '../MemoPage/MemoResult'

import note from './note.svg';
import remind from './remind.svg';
import bubble from './bubble.svg';
import Layout from '../Layout';
import notice from '../TestPage/notice.svg';
import Sharer from './Sharer'

let jsPDF


export const askings = [
  '依據目前的症狀，我可能罹患什麼病？',
  '什麼時候用藥，用藥時需要注意的事項？',
  '我需要進行哪些治療來緩解我的症狀？',
  '藥物可能有哪些副作用？',
  '如果想更了解症狀，可以搜尋哪些資料？',
  '若症狀緩解，是否可以提早停藥？',
  '我可以怎麼預防再發生同樣的症狀？'
]

const DownloadModule = ({ text, onClick, disabled, ...props }) => (
  <Flex
    width={mobileOrDesktop(1, 1/2)}
    alignItems="center"
    py={mobileOrDesktop('0.5em', 0)}
    {...props}
  >
    <Box mr="1em">{text}</Box>
    <Box width="2.5em">
      <Circle  onClick={onClick} disabled={disabled} is={Button.download}>
        <Download width="1.25em" />
      </Circle>
    </Box>
  </Flex>
)

class Notice extends PureComponent {
  state = {
    imgLoading: false,
    pdfLoading: false,
  }

  componentDidMount() {
    jsPDF = require('jspdf')
    window.html2canvas = require('html2canvas')
  }

  renderDom = createRef()

  setImgLoading = imgLoading => this.setState({ imgLoading })
  setPdfLoading = pdfLoading => this.setState({ pdfLoading })

  render() {
    const { memo, isMobile } = this.props
    const { imgLoading, pdfLoading } = this.state;
    const noticeList = (
      <Fragment>
        <Title icon={remind} fontSize="1.5em" title="看診時可向醫生詢問的問題參考" justifyContent="center" pt="1.5em" />
        <Flex flexWrap="wrap">
          {askings.map((a, i) => (
            <Box width={mobileOrDesktop(1, 1/2)} key={i}>
              <Flex
                alignItems="center"
                py="1em"
                mx="2em"
                borderBottom="1.5px solid"
                borderColor="titleBgOrange"
                key={i}
              >
                <StyledImage width="2.5em" src={bubble} />
                <Box width={1} mx="1em" fontSize={mobileOrDesktop('1em', '1.25em')}>{a}</Box>
              </Flex>
            </Box>
          ))}
        </Flex>
        <Text textAlign="center" mt="1em" fontSize={mobileOrDesktop('0.75em', '1em')}>✻ 想進一步了解藥物使用的方法、存放條件等注意事項，可以在領藥時進一步諮詢你的藥師！</Text>
      </Fragment>
    )
    return (
      <Layout>
        <Container py="2em">
          {memo && (
            <Box px={mobileOrDesktop('1em', '4em')} bg="titleBgOrange" py="1.5em" borderRadius="1em" mx={mobileOrDesktop('1em', '5em')}>
              <Title
                imgwidth="5em"
                justifyContent={mobileOrDesktop('center', 'auto')}
                flexWrap="wrap"
                width={mobileOrDesktop(1, null)}
                icon={note}
                title="恭喜你完成看診備忘錄"
                description={'看診前準備只剩最後一步了！\n預習看診時可以詢問的問題，見到醫生照著問就會沒問題！'}
              />
            </Box>
          )}
          {noticeList}
          <Box px="4em" bg="titleBgOrange" py="2em" borderRadius="1em" mt="3em">
            <Text textAlign="left" color="titleBlue">
              看醫生時{memo ? '同時下載「看診備忘錄」及「參考問題」' : '可以下載「參考問題」'}，提醒自己該跟醫師確認的事項
            </Text>
            <Flex mt="1em" flexWrap="wrap">
              <DownloadModule
                text={`下載為適合${isMobile ? '手機螢幕' : '直接分享'}的圖片`}
                pr={mobileOrDesktop(0, '2em')}
                justifyContent={mobileOrDesktop('space-between', 'flex-end')}
                borderRight={mobileOrDesktop('none', '1px solid')}
                disabled={imgLoading}
                onClick={() => {
                  this.setImgLoading(true)
                  domtoimage.toBlob(this.renderDom.current, { bgcolor: theme.colors.lightBgOrange })
                    .then((blob) => {
                      this.setImgLoading(false)
                      saveAs(blob, 'doctor-memo.png');
                    });
                }}
              />
              <DownloadModule
                text="下載為可列印的PDF檔案"
                pl={mobileOrDesktop(0, '2em')}
                justifyContent={mobileOrDesktop('space-between', 'unset')}
                borderLeft={mobileOrDesktop('none', '1px solid')}
                borderTop={mobileOrDesktop('1px solid', 'none')}
                disabled={pdfLoading}
                onClick={() => {
                  this.setPdfLoading(true)
                  const doc = new jsPDF({
                    orientation: 'landscape',
                  });

                  doc.html(this.renderDom.current, {
                    callback: (pdf) => {
                      this.setPdfLoading(false)
                      pdf.save();
                    }
                  });
                }}
              />
            </Flex>
          </Box>
          {memo && (
            <Title
              flexWrap="wrap"
              width={mobileOrDesktop(1, '6em')}
              icon={notice}
              textAlign={mobileOrDesktop('center', 'left')}
              fontSize="1.25em"
              desfontSize="1em"
              fontWeight="normal"
              title="看診後，別忘了再回來紀錄醫生的回答"
              description="幫助你未來有效追蹤自己的病況！"
              justifyContent="center"
              pt="1.5em"
            />
          )}
          {memo ? (
            <Sharer my="2em">分享看診好幫手給親友</Sharer>
          ) : (
            <Box textAlign="center" my="2em">
              <Button is={Link} to="/memo/create">製作看診備忘綠</Button>
            </Box>
          )}
        </Container>
        {createPortal((
          <Box position="absolute" top="-9999px" left="-999px" opacity="0">
            <div ref={this.renderDom}>
              <Container py="2em">
                {memo && (
                  <DateCard>
                    <MemoResult
                      dots={memo.dots}
                      answers={memo.answers}
                    />
                  </DateCard>
                )}
                {noticeList}
              </Container>
            </div>
          </Box>
        ), document.getElementById('___gatsby'))}
      </Layout>
    );
  }
}

export default withResponsive(Notice);
